import React, { useEffect } from "react";
// Components
import HomeCategory from "./../HomeCategory/HomeCategory";
// Slider Data
import { categoryOptions } from "./SliderOptions.jsx";
// Swiper
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper-bundle.min.css";
// Redux
import { useDispatch, useSelector } from "react-redux";
import { get_categories } from "./../../redux/actions/products";
import { Link } from "react-router-dom";
import { selectCategories } from "../../redux/selectors/products";
import { useTranslation } from "react-i18next";
import LoadingCategory from "./../HomeCategory/LoadingCategory";

const mapState = (state) => ({
	categories: selectCategories(state),
});

function CategorySlider() {
	const dispatch = useDispatch();
	const { categories } = useSelector(mapState);
	const { i18n } = useTranslation();

	useEffect(() => {
		if (Array.isArray(categories) && categories.length < 1) {
			dispatch(get_categories(i18n.language));
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [categories]);

	if (Array.isArray(categories) && categories.length >= 1) {
		return (
			<Swiper {...categoryOptions}>
				{categories.slice(0, 10).map((category) => {
					return (
						<SwiperSlide key={category.id}>
							<Link
								to={`/category/${category.slug}`}
								className="text-decoration-none text-dark"
							>
								<HomeCategory item={category} />
							</Link>
						</SwiperSlide>
					);
				})}
			</Swiper>
		);
	} else {
		return (
			<Swiper {...categoryOptions}>
				{[...Array(20)].map((x, i) => (
					<SwiperSlide key={x}>
						<LoadingCategory />
					</SwiperSlide>
				))}
			</Swiper>
		);
	}
}

export default CategorySlider;
