import React, { Suspense } from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { HomeCategoriesData } from "./HomeCategoriesData";

const ProductSlider = React.lazy(() => import("./../Slider/ProductSlider"));

function HomeProducts() {
	const { t } = useTranslation(["home"]);
	return (
		<div className="container pb-5">
			{HomeCategoriesData.map((category) => {
				const categoryName = t(`categories.${category.name}`);
				return (
					<div className="row" key={category.id}>
						<header className="section-heading d-flex justify-content-between  my-4">
							<h3 className="section-title" style={{ fontWeight: "bold" }}>
								{categoryName}
							</h3>

							<Link to={category?.link} className="text-decoration-none ">
								<span
									class="badge rounded-pill text-white px-2-5 py-1"
									style={{ backgroundColor: "#6270FB", fontSize: "16px" }}
								>
									<i className="bi-arrow-right-circle-fill"></i>
								</span>
							</Link>
						</header>
						<Suspense fallback={<div>–ó–∞–≥—Ä—É–∑–∫–∞...</div>}>
							<ProductSlider confi={category.api_link} />
						</Suspense>
					</div>
				);
			})}
		</div>
	);
}

export default HomeProducts;
