import React, { useState, useEffect } from "react";
// Swiper
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper-bundle.min.css";
import SwiperCore, {
	Pagination,
	Navigation,
	Keyboard,
	Autoplay,
	Zoom,
} from "swiper";
// Slider Data
import { imageOptions } from "./SliderOptions.jsx";
// import { items } from "./ImageSliderData";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { load_sliders } from "../../redux/actions/products.js";
import LoadingImageSlider from "./LoadingImageSlider.jsx";

SwiperCore.use([Navigation, Pagination, Autoplay, Keyboard, Zoom]);

function ImageSlider() {
	const dispatch = useDispatch();
	const [sliders, setSliders] = useState([]);
	useEffect(() => {
		if (!Array.isArray(sliders) || sliders.length < 1) {
			dispatch(load_sliders()).then((result) => {
				setSliders(result);
			});
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);
	const ready_sliders = (
		<SwiperSlide key="Slider">
			<Link to="/">
				<LoadingImageSlider />
			</Link>
		</SwiperSlide>
	);
	// const ready_sliders = items.map((item) => {
	// 	return (
	// 		<SwiperSlide key={item.alt}>
	// 			<Link to={item.link}>
	// 				<div
	// 					className="slider-image"
	// 					style={{
	// 						backgroundImage: `url(${item.src})`,
	// 					}}
	// 				></div>
	// 			</Link>
	// 		</SwiperSlide>
	// 	);
	// });

	return (
		<Swiper
			{...imageOptions}
			className="image-slider"
			navigation={{
				nextEl: ".bi .bi-arrow-right-circle",
				prevEl: ".bi .bi-arrow-left-circle",
			}}
		>
			{Array.isArray(sliders) && sliders.length >= 1
				? sliders.map((item) => {
						return (
							<SwiperSlide key={item.picture}>
								<Link to={item.link}>
									<div
										className="slider-image"
										style={{
											backgroundImage: `url(${item.picture})`,
										}}
									></div>
								</Link>
							</SwiperSlide>
						);
				  })
				: ready_sliders}
		</Swiper>
	);
}

export default ImageSlider;
