import {
	LOAD_PRODUCTS_SUCCESS,
	LOAD_SLIDERS_SUCCESS,
	GET_CATEGORIES_SUCCESS,
	LOAD_MORE_PRODUCTS_SUCCESS,
	ADD_COUPON,
	ADD_MESSAGE,
	LOAD_LOGO,
} from "../types/types";
import errors from "./../messages/errors.json";
import success from "./../messages/success.json";
import axios from "axios";
import { logout } from "./auth";

export const config = {
	headers: {
		"Content-Type": "application/json",
	},
	withCredentials: true,
};

// GET

export const load_products = (confi, old_products, lang) => async (
	dispatch
) => {
	const config = {
		headers: {
			"Content-Type": "application/json",
			"Accept-Language": lang,
		},
	};
	try {
		const res = await axios.get(
			`${process.env.REACT_APP_API_VERSION_URL}/products/?${confi}`,
			config
		);

		if (Array.isArray(old_products)) {
			dispatch({
				type: LOAD_MORE_PRODUCTS_SUCCESS,
				results: res.data.results,
				next: res.data.next,
				prev: res.data.previous,
				count: res.data.count,
				old_products: old_products,
			});
		} else {
			dispatch({
				type: LOAD_PRODUCTS_SUCCESS,
				results: res.data.results,
				next: res.data.next,
				prev: res.data.previous,
				count: res.data.count,
			});
		}

		return res.data.results;
	} catch (error) {
		// console.log(error.message);
	}
};
