// POST

export const publish_comment = (bodyInput, product_id) => async (dispatch) => {
	if (localStorage.getItem("access")) {
		const config = {
			headers: {
				"Content-Type": "application/json",
				Authorization: `JWT ${localStorage.getItem("access")}`,
				Accept: "application/json",
			},
		};

		const body = JSON.stringify({ body: bodyInput });
		try {
			const res = await axios.post(
				`${process.env.REACT_APP_API_VERSION_URL}/comments/${product_id}/`,
				body,
				config
			);
			dispatch({ type: ADD_MESSAGE, payload: success.success2 });
			return res.status;
		} catch (error) {
			dispatch(logout());
			dispatch({ type: ADD_MESSAGE, payload: success.success1 });
			return errors.error3;
		}
	} else {
		return errors.error2;
	}
};

export const add_coupon = (coupon) => async (dispatch) => {
	if (localStorage.getItem("access")) {
		const config = {
			headers: {
				"Content-Type": "application/json",
				Authorization: `JWT ${localStorage.getItem("access")}`,
				Accept: "application/json",
			},
		};

		const body = JSON.stringify({ coupon: coupon });
		try {
			const res = await axios.post(
				`${process.env.REACT_APP_API_VERSION_URL}/coupon/`,
				body,
				config
			);
			dispatch({ type: ADD_COUPON, payload: res.data });
			return res;
		} catch (error) {
			return errors.error3;
		}
	} else {
		return errors.error1;
	}
};

